/* ==========================================================================
   Components Crypto
   ========================================================================== */

.crypto__box {
  $cb: &;
  padding: 48px 24px;
  border-radius: 16px;
  text-align: center;
  transition: all 0.2s ease;
  box-shadow: 0 40px 40px 0 rgba(57, 23, 119, .03);
  position: relative;
  &:hover {
    background-color: color(secondary);
    transform: translateY(-4px);
    #{$cb}__title,
    #{$cb}__desc {
      color: color(white);
    }
    &__btn {
      min-width: 62px;
      transition: all .3s ease;
      $bt: &;
      &--round {
        font-size: 16px;
      }
    }
  }
  @include medium {
    padding: 40px 16px;
  }
  &__img {
    margin-bottom: 48px;
    @include large {
      margin-bottom: 32px;
    }
    @include extra-small {
      margin-bottom: 16px;
    }
    &__el {
      width: 80px;
      display: block;
      margin: 0 auto;
    }
  }
  &__title {
    margin-bottom: 40px;
    @include medium {
      margin-bottom: 32px;
    }
    @include extra-small {
      margin-bottom: 16px;
      @include font-size(h4);
    }
    &__small {
      @include font-size(h6);
      color: color(grey, 2);
    }
  }
  &__desc {
    color: color(grey, 3);
    margin-bottom: 24px;
  }
}
.crypto__link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}